import styled from 'styled-components'
import prims from '../../Primitives'

const H4 = styled.h4`
  padding: 0.35rem 0 .65rem 0;

  color: ${prims.colors.blue.dark.opaque};

  font-size: ${prims.fontsize.m};
  font-weight: bold;
  line-height: 2rem;
`

export default H4


